$breakpoints: (

    "sm" : 320px,
    "md" : 520px,
    "lg" : 800px,
    "xl" : 1000px,


);

@mixin sm {

    @media (max-width: map-get($breakpoints, "sm")) {

        @content;
        
    }
    
}

@mixin md {

    @media (max-width: map-get($breakpoints, "md")) {
        
        @content;
        
    }
    
}   

@mixin lg {

    @media (max-width: map-get($breakpoints, "lg")) {
        
        @content;
        
    }
    
}

@mixin xl {

    @media (max-width: map-get($breakpoints, "xl")) {
        
        @content;
        
    }
    
}

@mixin breakpoint($bp: 0) {

    @media (max-width: $bp) {

        @content;

    }

}



